import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FacilityService } from 'src/app/api/controllers/Facility';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';


import dayGridPlugin from '@fullcalendar/daygrid';

import * as moment from 'moment';
import * as _ from 'underscore';

@Component({
  selector: 'app-user-facility-calendar',
  templateUrl: './user-facility-calendar.component.html',
  styleUrls: ['./user-facility-calendar.component.scss']
})
export class UserFacilityCalendarComponent implements OnInit {

  calendarPlugins = [dayGridPlugin]; // important!

  calendarEvents:any = [];

  facilityBookingData:any;

  facilityCategoryData:any;
  facilitySlotData:any;

  isCalenderLoaded:boolean = false;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
	private facilityService:FacilityService,
	private lookupService: LookupService,
	private sharedService:SharedService,
	private sessionService:SessionService
  ) { }

  handleEventClick(arg){
  	
  	var id = arg.event.id;
  	this.router.navigate(['/user/facility-booking/edit-booking', id]);

  }

  getFacilityBookingData() {

	let params = {
		apartmentId: this.sessionService.apartmentId
	}

  	this.facilityService.getApartmentFacilitySlotsByApartmentId(params).subscribe((res:any) => {

      this.facilitySlotData = res.filter(item => {
        return item.isActive;
      })


    },
    error => {

	});
	
	let categoryParams = {
		apartmentId: this.sessionService.apartmentId
	}

  	this.facilityService.getApartmentFacilitiesByApartmentId(categoryParams).subscribe((res:any) => {


		this.facilityCategoryData = res.filter(item => {
			return item.isActive;
		})

		let bookingParams = {
			apartmentId: this.sessionService.apartmentId
		}

		this.facilityService.getApartmentFacilityBookingsByApartmentId(bookingParams).subscribe((res:any) => {

			this.facilityBookingData = res.filter(item => {
				return item.isActive && ( item.neededUserId == parseInt(this.sessionService.userId) )
			});


			_.each(this.facilityBookingData, booking => {

				var date = moment(booking.bookedForDate).format("YYYY-MM-DD");

				var nameItem = this.facilityCategoryData.filter( item => {
  					return item.apartmentFacilityId == booking.apartmentFacilityId;
  				});

  				var slotData = this.facilitySlotData.filter( item => {
  					return item.apartmentFacilitySlotId == booking.slotId;
  				});

				var beginTime = moment(slotData[0].slotBeginTime, 'HH:mm:ss').toDate();
				var slotBeginTime = moment(beginTime).format('HH:mm A');

				var EndTime = moment(slotData[0].slotEndTime, 'HH:mm:ss').toDate();
				var slotEndTime = moment(EndTime).format('HH:mm A');

  				var event = nameItem[0].facilityName + " ( " + slotBeginTime  + "-" + slotEndTime +" ) ";

				this.calendarEvents.push({ title: event, date: date, id: nameItem[0].apartmentFacilityId });


				this.isCalenderLoaded = true;

			});

		},
		error => {

		});
			      

    },
    error => {

    }); 

  }


  ngOnInit() {

  	this.getFacilityBookingData();

  }

}
