import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { FacilityBookingComponent } from './facility-booking.component';
import { AllFacilityBookingsComponent } from './components/all-facility-bookings/all-facility-bookings.component';
import { UserFacilityCalendarComponent } from './components/user-facility-calendar/user-facility-calendar.component';
import { FacilityCreateBookingComponent } from 'src/app/shared/components/facility-create-booking/facility-create-booking.component';

const routes: Routes = [
	{ path: '', redirectTo: 'all-bookings', pathMatch: 'full'},
	{ path: 'all-bookings', component: AllFacilityBookingsComponent},
	{ path: 'create-booking', component: FacilityCreateBookingComponent},
	{ path: 'edit-booking/:id', component: FacilityCreateBookingComponent},
	{ path: 'calendar', component: UserFacilityCalendarComponent},
	{ path: '**', redirectTo: 'all-bookings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FacilityBookingRoutingModule { }
