import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-all-facility-bookings',
  templateUrl: './all-facility-bookings.component.html',
  styleUrls: ['./all-facility-bookings.component.scss']
})
export class AllFacilityBookingsComponent implements OnInit { 
@ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
columnData:any;
bookingListData:any;
bookingSearch:string;
modalService:any;
isBookingDataLoaded:boolean = false;
totalItems:number = 0;

constructor(
  private facilityService:FacilityService,
  private sessionService:SessionService,
  private router: Router,
  private sharedService:SharedService,
  public dialog: MatDialog,
) {}

  @HostListener('window:onEditFacilityBooking', ['$event.detail'])
  editBooking(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.router.navigate(['user/facility/edit-booking',dataRecord.apartmentFacilityBookingId]);
  }

  @HostListener('window:onViewFacilityBooking', ['$event.detail'])
  viewBooking(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.router.navigate(['user/facility/view-booking',dataRecord.apartmentFacilityBookingId]);
  }

  @HostListener('window:ondeleteMeeting', ['$event.detail'])
  ondeleteSlot(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.modalService.showConfirmModal(dataRecord.apartmentFacilityBookingId);
  }

  searchData() {
    if(this.bookingSearch != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.bookingSearch;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'BookingList');
  }

  getBookingList() {
    let params = {
      apartmentId: this.sessionService.apartmentId,
      apartmentBlockUnitUserId: this.sessionService.apartmentBlockUnitUserId
    }
    this.facilityService.getApartmentFacilityBookingsByBlockunituserId(params).subscribe((res:any) => {
      if(res.length > 0) {
        this.totalItems = res.length;
        let facility = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.bookingListData = new jqx.dataAdapter(facility);
      }
      this.isBookingDataLoaded = true;
    })
  }

  ngOnInit() {
    this.getBookingList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Booking Id',
      datafield: 'serialNo',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Facility Name',
      datafield: 'apartmentFacilityName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Event Date',
      datafield: 'bookedForDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Event Time',
      datafield: 'bookedFromTime',
        cellsrenderer: (row, column, value) => {
          let time,fromTime,toTime = this.bookingListData.loadedData[row].bookedToTime;
          if(value && toTime)  {
          fromTime = moment(value,'HH:mm:ss').format('hh:mm A');
          toTime = moment(toTime,'HH:mm:ss').format('hh:mm A');
          time = `${fromTime} - ${toTime}`
          } else {
            time = '-'
          }
        return '<div class="jqx-custom-inner-cell">'+ time +'</div>'
      },
      cellsalign: 'center', 
      align: 'center',
      minwidth: 160,
      renderer: columnrenderer
    },{
      text: 'Total Hours',
      datafield: 'bookedToTime',
      cellsrenderer: (row, column, value) => {
        let time,toTime,fromTime = this.bookingListData.loadedData[row].bookedFromTime;
        if(fromTime && value) {
          fromTime = moment(fromTime,'HH:mm:ss').format();
          toTime = moment(value,'HH:mm:ss').format();
          time = moment.duration(moment(toTime).diff(moment(fromTime)));
          if(time.minutes() == 0) {
            time = `${time.hours()}hrs`
          } else {
            time = `${time.hours()}hrs ${time.minutes()}mins`
          }
          if(time == '23hrs 58mins') {
            time = '24hrs'
          }
        } else {
          time = '-'
        }
        return '<div class="jqx-custom-inner-cell ml-4">'+ time +'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Rate Type',
      datafield: 'rateBaseIdName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Cost',
      datafield: 'amount',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Discount',
      datafield: 'discount',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Final Bill',
      datafield: 'totalBillAmount',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Booked For',
      datafield: 'isBookingforGuest',
      cellsrenderer: (row, column, value) => {
        value =  value ? 'Guest' : 'Unit User';
        return '<div class="jqx-custom-inner-cell">'+ value +'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Status',
      datafield: 'facilityBookingStatusId_Label',
      cellsrenderer: (row, column, label) => {
        let className  = label == 'Rejected' ? 'cancelled' : label.toLowerCase();
        return '<div class="jqx-custom-inner-cell"><span class="w-100 badge small min text-capitalize '+ className +'">'+ label +'</span></div>'
      },
      cellsalign: 'center', 
      align: 'center',
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions">'
        +'<a href="javascript:void(0)" class="mr-3" onClick="editFacilityBooking('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
        +'<a href="javascript:void(0)" class="mr-2" onClick="showConfirmDelete(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a>'
        +'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    }];
    //delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {
      if(item != null) {
        var params = {
          apartmentFacilityBookingId: item,
          deleteBy: parseInt(this.sessionService.userId)
        }
        this.facilityService.deleteApartmentFacilityBooking(params).subscribe((res:any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          this.getBookingList();
        });
      }
    });
  }
}

function editFacilityBooking(row) {  
  var event = new CustomEvent('onEditFacilityBooking', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editFacilityBooking = editFacilityBooking;

function viewFacilityBooking(row) {
  var event = new CustomEvent('onViewFacilityBooking', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewFacilityBooking = viewFacilityBooking;

function showConfirmDelete(row) {
  var event = new CustomEvent('ondeleteMeeting', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDelete = showConfirmDelete;
